import React, {useState} from 'react'
import Affairs from './affairs/Affairs'
import s2 from '../../s1-main/App.module.css'

/*
* 1 -   AffairPriorityType, AffairType                                       **
* 2 -     defaultAffairs                                             **
* 3 -      filterAffairs                   **
* 4 -   3   deleteAffair
* 5 -     useState  affairs
* 6 -      deleteAffairCallback
* 7 -   Affairs.tsx   
* 8 -   Affairs.tsx    setAll, setHigh, setMiddle, setLow
* 9 -   Affair.tsx   
* 10 -   Affair.tsx   deleteCallback  
* 11 -   Affair.tsx   
* */

// types
export type AffairPriorityType = 'high' | 'low' | 'middle'  // need to fix any
export type AffairType = {
    _id: number // need to fix any
    name: string // need to fix any
    priority: AffairPriorityType
}
export type FilterType = 'all' | AffairPriorityType

// constants
const defaultAffairs: Array<AffairType> = [ // need to fix any
    {_id: 1, name: 'React', priority: 'high'}, //     name     , ...priority  !
    {_id: 2, name: 'anime', priority: 'low'},
    {_id: 3, name: 'games', priority: 'low'},
    {_id: 4, name: 'work', priority: 'high'},
    {_id: 5, name: 'html & css', priority: 'middle'},
]

// pure helper functions
export const filterAffairs = (affairs: Array<AffairType>, filter: FilterType): any => { // need to fix any
    //   "all"...    ,   ?
    //     ...
    if (filter === 'high') {
            return affairs.filter(af => af.priority === 'high')
    }
    if (filter === 'middle') {
        return affairs.filter(af  => af.priority === 'middle')
    }
    if (filter === 'low') {
        return affairs.filter(af => af.priority === 'low')
    }

    return affairs // need to fix
}
export const deleteAffair = (affairs: Array<AffairType>, _id: number): any => { // need to fix any
    // need to fix
    //     filter  affairs
    return affairs.filter(af => af._id !== _id )
}

function HW2() {
    const [affairs, setAffairs] = useState<any>(defaultAffairs) // need to fix any
    const [filter, setFilter] = useState<FilterType>('all')

    const filteredAffairs = filterAffairs(affairs, filter)

    //const deleteAffairCallback = (_id: number) => { // need to fix any
    const deleteAffairCallback = (_id: number) => { // need to fix any
        // need to fix
        //    - ,    deleteAffair
        // setAffairs( ( ))
        setAffairs(() => deleteAffair(affairs, _id))
    }

    return (
        <div id={'hw2'}>
            <div className={s2.hwTitle}>Homework #2</div>
            <div className={s2.hw}>
                <Affairs
                    data={filteredAffairs}
                    filter={filter}          //  useState !
                    setFilter={setFilter}    //  useState !
                    deleteAffairCallback={deleteAffairCallback}
                />
            </div>
        </div>
    )
}

export default HW2
